import React, { Fragment, useRef, useState, useEffect } from 'react';
import ButtonText from '../../button-text';
import Checkbox from '../../checkbox';
import CheckboxErrorMessage from '../../checkbox-error-message';
import CheckboxList from '../../checkbox-list';
import CheckboxListItem from '../../checkbox-list-item';
import CheckboxSet from '../../checkbox-set';
import CheckboxSetLegend from '../../checkbox-set-legend';
import Col from '../../col';
import Grid from '../../grid';
import Icon from '../../icon';
import Row from '../../row';
import Typography from '../../typography';

const CheckboxExample = () => {
  const ref = useRef();
  const refOneValidation = useRef();
  const refTwoValidation = useRef();
  const refThreeValidation = useRef();
  const refValidationArray = [refOneValidation, refTwoValidation, refThreeValidation];
  const [validationTrigger, setValidation] = useState(false);
  const [showIndividualError, setIndividualError] = useState(false);
  const [showGroupError, setGroupError] = useState(false);
  const [stateOne, setStateOne] = useState({
    items: [
      {
        checked: false,
        id: 'item-1',
        label: 'One',
        value: 'one'
      },
      {
        checked: true,
        id: 'item-2',
        label: 'Two',
        value: 'two'
      },
      {
        checked: false,
        id: 'item-3',
        label: 'Three',
        value: 'three'
      },
      {
        checked: false,
        id: 'item-4',
        label: 'Four',
        value: 'four'
      }
    ]
  });

  const [stateTwo, setStateTwo] = useState({ values : ['6', '8'] });
  const [stateThree, setStateThree] = useState({
    checked: false,
    id: 'validation-individual',
    invalid: false,
    label: 'I have read and agree to the Terms and Conditions',
    required: true,
    value: '1'
  });
  const [stateFour, setStateFour] = useState({
    list: [
      {
        checked: false,
        children: [
          {
            checked: true,
            id: 'checkbox-indeterminate-100',
            label: 'Pepperoni',
            value: '1-10'
          },
          {
            checked: false,
            id: 'checkbox-indeterminate-101',
            label: 'Sausage',
            value: '1-11'
          },
          {
            checked: false,
            id: 'checkbox-indeterminate-102',
            label: 'BBQ Chicken',
            value: '1-12'
          }
        ],
        disabled: false,
        id: 'checkbox-indeterminate-1',
        indeterminate: true,
        label: 'All Meats',
        value: '1'
      },
      {
        checked: false,
        children: [
          {
            checked: false,
            id: 'checkbox-indeterminate-200',
            label: 'Mushrooms',
            value: '2-13'
          },
          {
            checked: false,
            id: 'checkbox-indeterminate-201',
            label: 'Black Olives',
            value: '2-14'
          },
          {
            checked: false,
            id: 'checkbox-indeterminate-202',
            label: 'Pineapple',
            value: '2-15'
          },
          {
            checked: false,
            id: 'checkbox-indeterminate-203',
            label: 'Red Onion',
            value: '2-16'
          }
        ],
        disabled: false,
        id: 'checkbox-indeterminate-2',
        indeterminate: false,
        label: 'All Vegetables',
        value: '2'
      },
      {
        checked: true,
        disabled: false,
        id: 'checkbox-indeterminate-3',
        indeterminate: false,
        label: 'Extra Cheese',
        value: '3'
      }
    ]
  });

  const [stateFive, setStateFive] = useState({
    items: [
      {
        checked: false,
        id: 'validation-1',
        invalid: false,
        label: 'Regular',
        required: true,
        value: '1'
      }, {
        checked: false,
        id: 'validation-2',
        invalid: false,
        label: 'Deep Dish',
        required: true,
        value: '2'
      }, {
        checked: false,
        id: 'validation-3',
        invalid: false,
        label: 'Thin Crust',
        required: true,
        value: '3'
      }
    ]
  });

  useEffect(() => {
    if (stateFive.items[0].invalid === true && validationTrigger === true) {
      refOneValidation.current.focus();
    } else if (stateFive.items[1].invalid === true && validationTrigger === true) {
      refTwoValidation.current.focus();
    } else if (stateFive.items[2].invalid === true && validationTrigger === true) {
      refThreeValidation.current.focus();
    }
    setValidation(false);
  }, [validationTrigger, stateFive.items]);

  const handleChangeOne = e => setStateOne({
    items: [
      ...stateOne.items.map(item => ({
        ...item,
        checked: e.target.id === item.id ? !item.checked : item.checked
      }))
    ]
  });

  const handleChangeTwo = e => {
    const index = stateTwo.values.findIndex(v => v === e.target.value);
    if (index !== -1) {
      return setStateTwo({
        values: [...stateTwo.values.slice(0, index), ...stateTwo.values.slice(index + 1)]
      });
    }

    return setStateTwo({
      values: [...stateTwo.values, e.target.value]
    });
  };

  const handleChangeThree = e => setStateThree({
    ...stateThree,
    checked: e.target.id === stateThree.id ? !stateThree.checked : stateThree.checked,
    invalid: e.target.id === stateThree.id && stateThree.invalid ? stateThree.checked : stateThree.invalid
  });

  const handleSubmitThree = () => {
    let invalidChecker = false;
    if (stateThree.checked === false) {
      invalidChecker = true;
      ref.current.focus();
      setIndividualError(true);
    } else {
      invalidChecker = false;
      setIndividualError(false);
    }

    setStateThree({
      ...stateThree,
      invalid: invalidChecker
    });
  };

  const handleChangeFour = (itemId, childId) => {
    const parentIndex = stateFour.list.findIndex(item => item.id === itemId);
    const parent = stateFour.list[parentIndex];

    if (childId) {
      const childIndex = parent.children.findIndex(child => child.id === childId);
      const child = parent.children[childIndex];
      parent.children = [...parent.children.slice(0, childIndex), { ...child, checked: !child.checked }, ...parent.children.slice(childIndex + 1)];
      parent.checked = parent.children.filter(c => c.checked === true).length === parent.children.length;
      parent.indeterminate = parent.children.filter(c => c.checked === true).length > 0;
    }

    if (typeof childId === 'undefined') {
      parent.checked = !parent.checked;
      parent.indeterminate = false;
      if (parent.children) {
        parent.children = parent.children.map(child => ({ ...child, checked: parent.checked }));
      }
    }

    setStateFour({
      list: [
        ...stateFour.list.slice(0, parentIndex),
        parent,
        ...stateFour.list.slice(parentIndex + 1)
      ]
    });
  };

  const handleChangeFive = e => setStateFive({
    items: [
      ...stateFive.items.map(item => ({
        ...item,
        checked: e.target.id === item.id ? !item.checked : item.checked,
        invalid: e.target.id === item.id && item.invalid ? item.checked : item.invalid
      }))
    ]
  });

  const handleSubmitFive = () => {
    let invalidChecker = false;
    if (stateFive.items.filter(item => item.checked).length === 0) {
      invalidChecker = true;
      setGroupError(true);
    } else {
      invalidChecker = false;
      setGroupError(false);
    }

    setStateFive({
      items: [
        ...stateFive.items.map(item => ({
          ...item,
          invalid: invalidChecker
        }))
      ]
    });

    setValidation(true);
  };

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Checkbox (stateful)</Typography>
          <Typography>
            All checkbox props are mapped to the checkbox
            component, e.g. defaultChecked, onClick, etc
          </Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <CheckboxSet>
            <CheckboxSetLegend>Checkbox Options</CheckboxSetLegend>
            <CheckboxList>
              <CheckboxListItem>
                <Checkbox
                  defaultChecked
                  name="example"
                  label="First option"
                  value="1"
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  name="example"
                  label="Second option"
                  value="2"
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  name="example"
                  label="Third option"
                  value="3"
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  name="example"
                  label="Fourth option"
                  value="4"
                  disabled
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  name="example"
                  label="Fifth option"
                  value="5"
                  defaultChecked
                  disabled
                />
              </CheckboxListItem>
            </CheckboxList>
          </CheckboxSet>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Checkbox (stateless) data approach</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <CheckboxSet>
            <CheckboxSetLegend>Stateless Data Approach Checkbox Options</CheckboxSetLegend>
            <CheckboxList>
              {
                stateOne.items.map((item, k) => (
                  <CheckboxListItem key={k}>
                    <Checkbox
                      checked={item.checked}
                      id={item.id}
                      label={item.label}
                      name="checkbox-stateless"
                      onChange={handleChangeOne}
                      value={item.value}
                    />
                  </CheckboxListItem>
                ))
              }
            </CheckboxList>
          </CheckboxSet>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Checkbox (stateless) composition approach</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography>Selected values [{stateTwo.values.join(', ')}] </Typography>
          <CheckboxSet>
            <CheckboxSetLegend>Stateless Composition Approach Checkbox Options</CheckboxSetLegend>
            <CheckboxList>
              <CheckboxListItem>
                <Checkbox
                  checked={stateTwo.values.includes("6")}
                  name="example"
                  label="First option"
                  value="6"
                  onChange={handleChangeTwo}
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  checked={stateTwo.values.includes("7")}
                  name="example"
                  label="Second option"
                  value="7"
                  onChange={handleChangeTwo}
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  checked={stateTwo.values.includes("8")}
                  name="example"
                  label="Third option"
                  value="8"
                  onChange={handleChangeTwo}
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  checked={stateTwo.values.includes("9")}
                  name="example"
                  label="Fourth option"
                  value="9"
                  onChange={handleChangeTwo}
                  disabled
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  checked={stateTwo.values.includes("10")}
                  name="example"
                  label="Fifth option"
                  value="10"
                  onChange={handleChangeTwo}
                  disabled
                />
              </CheckboxListItem>
            </CheckboxList>
          </CheckboxSet>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Indeterminate</Typography>
          <Typography>This standalone indeterminate checkbox is for developer example only. <br />
             To see an accessible indeterminate checkbox in action, view the Checkbox Set with Validation below.</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
        <CheckboxSet>
          <CheckboxSetLegend>Indeterminate checkbox examples</CheckboxSetLegend>
          <CheckboxList>
            <CheckboxListItem>
              <Checkbox
                name="example"
                label="Indeterminate Checkbox"
                id="checkbox-test-21"
                value="2"
                indeterminate
              />
            </CheckboxListItem>
            <CheckboxListItem>
             <Checkbox
                name="example"
                label="Disabled Indeterminate Checkbox"
                id="checkbox-test-22"
                value="2"
                disabled
                indeterminate
              />
            </CheckboxListItem>
          </CheckboxList>
        </CheckboxSet>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Checkbox with Validation</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <CheckboxSet>
            <CheckboxSetLegend
              invalid={showIndividualError}
              aria-describedby="single-validation-error-message"
            >
              Terms and Conditions
            </CheckboxSetLegend>
            <CheckboxList>
              <CheckboxListItem>
                <Checkbox
                  checked={stateThree.checked}
                  id={stateThree.id}
                  invalid={stateThree.invalid}
                  aria-invalid={stateThree.invalid}
                  label={stateThree.label}
                  onChange={handleChangeThree}
                  ref={ref}
                  required={stateThree.required}
                  value={stateThree.value}
                />
              </CheckboxListItem>
            </CheckboxList>
          </CheckboxSet>
        <CheckboxErrorMessage id="single-validation-error-message">
          {
            showIndividualError === true
              ? <Fragment><Icon
                    name="error"
                    resolution="tiny"
                  />Error: Please check the box.</Fragment>
              : ''
          }
          </CheckboxErrorMessage>
          <ButtonText
            style={{ display: "block" }}
            onClick={handleSubmitThree}
          >
            Submit (test validation)
          </ButtonText>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Checkbox Group with Indeterminate</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <CheckboxSet>
            <CheckboxSetLegend>Select Pizza Toppings</CheckboxSetLegend>
            <CheckboxList>
              {
                stateFour.list.map((item, i) => (
                  <CheckboxListItem key={i}>
                    <Checkbox
                      checked={item.checked}
                      disabled={item.disabled}
                      indeterminate={item.indeterminate}
                      id={item.id}
                      label={item.label}
                      name="pizza-topping-group"
                      onChange={() => handleChangeFour(item.id)}
                      value={item.value}
                    />
                    {
                      item.children
                        ? <CheckboxList type="inner">
                          {item.children.map((child, k) => (
                            <CheckboxListItem key={k}>
                              <Checkbox
                                checked={child.checked}
                                id={child.id}
                                label={child.label}
                                name="pizza-topping-item"
                                onChange={() => handleChangeFour(item.id, child.id)}
                                value={child.value}
                              />
                            </CheckboxListItem>
                          ))}
                        </CheckboxList>
                        : null
                    }
                  </CheckboxListItem>
                ))
              }
            </CheckboxList>
          </CheckboxSet>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Checkbox Set</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <CheckboxSet>
            <CheckboxSetLegend>Select Drink Toppings</CheckboxSetLegend>
            <CheckboxList>
              <CheckboxListItem>
                <Checkbox
                  name="drink-toppings"
                  label="Boba"
                  value="1"
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  name="drink-toppings"
                  label="Lychee jelly"
                  value="2"
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  name="drink-toppings"
                  label="Mango star jelly"
                  value="3"
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  name="drink-toppings"
                  label="Aloe vera pieces (not available)"
                  value="4"
                  disabled
                />
              </CheckboxListItem>
              <CheckboxListItem>
                <Checkbox
                  name="drink-toppings"
                  label="Coconut jelly (included)"
                  value="5"
                  defaultChecked
                  disabled
                />
              </CheckboxListItem>
            </CheckboxList>
          </CheckboxSet>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Checkbox Set with Validation (BYO-State)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <CheckboxSet>
            <CheckboxSetLegend
              invalid={showGroupError}
              aria-describedby="validation-error-message"
            >
              Pizza topping
            </CheckboxSetLegend>
            <CheckboxList aria-label="required">
              {
                stateFive.items.map((item, k) => (
                  <CheckboxListItem key={k}>
                    <Checkbox
                      checked={item.checked}
                      id={item.id}
                      invalid={item.invalid}
                      label={item.label}
                      name="checkbox-validation-data-example"
                      onChange={handleChangeFive}
                      ref={refValidationArray[k]}
                      value={item.value}
                    />
                  </CheckboxListItem>
                ))
              }
            </CheckboxList>
            <CheckboxErrorMessage id="validation-error-message">
              {
                showGroupError === true
                  ? <Fragment><Icon name="error" resolution="tiny" />
                    Error: Please select at least one pizza topping.</Fragment>
                  : ''
              }
            </CheckboxErrorMessage>
          </CheckboxSet>
          <ButtonText
            onClick={handleSubmitFive}
          >
            Submit (test validation)
          </ButtonText>
        </Col>
      </Row>
    </Grid>
  );
};

export default CheckboxExample;
