import React, { forwardRef } from 'react';
import { bool, elementType, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-checkbox-set-legend';
const CSS_STATE = 'vds-state';

const CheckboxSetLegend = forwardRef(({ children, className, invalid, tag: Tag, ...remainingProps }, ref) => (
  <Tag
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--invalid`]: Boolean(invalid)
    }, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </Tag>
));

CheckboxSetLegend.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
  * Invalid
  */
  invalid: bool,

  /**
  * Tag
  */
  tag: elementType
};

CheckboxSetLegend.defaultProps = {
  tag: 'legend'
};

CheckboxSetLegend.displayName = 'CheckboxSetLegend';

export default CheckboxSetLegend;
