import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-checkbox-list-item';

const CheckboxListItem = ({ children, className, ...remainingProps }) => (
  <li
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </li>
);

CheckboxListItem.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

CheckboxListItem.displayName = 'CheckboxListItem';

export default CheckboxListItem;
