import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-checkbox-set';

const CheckboxSet = ({ children, className, ...remainingProps }) => (
  <fieldset
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </fieldset>
);

CheckboxSet.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

CheckboxSet.displayName = 'CheckboxSet';

export default CheckboxSet;
