import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-checkbox-list';

const CheckboxList = ({ children, className, orientation, type, ...remainingProps }) => (
  <ul
    className={classnames(CSS_PREFIX, {
      [`${CSS_PREFIX}-${type}`]: Boolean(type),
      [`${CSS_PREFIX}--${orientation}`]: Boolean(orientation)
    }, className)}
    {...remainingProps}
  >
    {children}
  </ul>
);

CheckboxList.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * orientation
   */
  orientation: oneOf(['horizontal', 'vertical']),

  /**
   * Type
   */
  type: oneOf([
    'inner'
  ])
};

CheckboxList.displayName = 'CheckboxList';

export default CheckboxList;
